import { ReactComponent as Alert } from "bootstrap-icons/icons/exclamation-triangle-fill.svg";
import { ReactComponent as ArrowLeft } from "bootstrap-icons/icons/arrow-left.svg";
import { ReactComponent as Book } from "bootstrap-icons/icons/book.svg";
import { ReactComponent as BoxArrowUpRight } from "bootstrap-icons/icons/box-arrow-up-right.svg";
import { ReactComponent as CaretDownFill } from "bootstrap-icons/icons/caret-down-fill.svg";
import { ReactComponent as CaretUpFill } from "bootstrap-icons/icons/caret-up-fill.svg";
import { ReactComponent as ChatDots } from "bootstrap-icons/icons/chat-dots.svg";
import { ReactComponent as CheckCircle } from "bootstrap-icons/icons/check-circle.svg";
import { ReactComponent as ChevronLeft } from "bootstrap-icons/icons/chevron-left.svg";
import { ReactComponent as ChevronRight } from "bootstrap-icons/icons/chevron-right.svg";
import { ReactComponent as Clipboard } from "bootstrap-icons/icons/clipboard.svg";
import { ReactComponent as ClipboardCheck } from "bootstrap-icons/icons/clipboard-check.svg";
import { ReactComponent as Code } from "bootstrap-icons/icons/code.svg";
import { ReactComponent as Cpu } from "bootstrap-icons/icons/cpu.svg";
import { ReactComponent as Download } from "bootstrap-icons/icons/download.svg";
import { ReactComponent as Facebook } from "bootstrap-icons/icons/facebook.svg";
import { ReactComponent as FileEarmark } from "bootstrap-icons/icons/file-earmark.svg";
import { ReactComponent as FileZip } from "bootstrap-icons/icons/file-zip.svg";
import { ReactComponent as Filter } from "bootstrap-icons/icons/filter.svg";
import { ReactComponent as FolderFill } from "bootstrap-icons/icons/folder-fill.svg";
import { ReactComponent as Front } from "bootstrap-icons/icons/front.svg";
import { ReactComponent as GearFill } from "bootstrap-icons/icons/gear-fill.svg";
import { ReactComponent as Git } from "bootstrap-icons/icons/git.svg";
import { ReactComponent as Github } from "bootstrap-icons/icons/github.svg";
import { ReactComponent as Heart } from "bootstrap-icons/icons/heart.svg";
import { ReactComponent as InfoCircle } from "bootstrap-icons/icons/info-circle.svg";
import { ReactComponent as Link } from "bootstrap-icons/icons/link-45deg.svg";
import { ReactComponent as LinkedIn } from "bootstrap-icons/icons/linkedin.svg";
import PropTypes from "prop-types";
import { ReactComponent as Search } from "bootstrap-icons/icons/search.svg";
import { ReactComponent as Stack } from "bootstrap-icons/icons/stack.svg";
import { ReactComponent as Star } from "bootstrap-icons/icons/star.svg";
import { ReactComponent as Tag } from "bootstrap-icons/icons/tag.svg";
import { ReactComponent as Tags } from "bootstrap-icons/icons/tags.svg";
import { ReactComponent as Twitter } from "bootstrap-icons/icons/twitter.svg";
import { ReactComponent as X } from "bootstrap-icons/icons/x.svg";

function Icon(props) {
  let svgContent = "";
  let svgSize = props.size;
  let svgViewBox = "0 0 24 24";
  const svgProps = {
    width: props.size,
    height: props.size,
  };
  if (props.className) svgProps["className"] = props.className;
  switch (props.name) {
    /** package type icons */
    case "package-library":
      return <Front {...svgProps} />;
    case "package-platform":
      return <Stack {...svgProps} />;
    case "package-tool":
      return <GearFill {...svgProps} />;

    /** other icons */
    case "alert":
      return <Alert {...svgProps} />;
    case "arrow-left":
      return <ArrowLeft {...svgProps} />;
    case "book":
      return <Book {...svgProps} />;
    case "box-arrow-up-right":
      return <BoxArrowUpRight {...svgProps} />;
    case "caret-up-fill":
      return <CaretUpFill {...svgProps} />;
    case "caret-down-fill":
      return <CaretDownFill {...svgProps} />;
    case "chat-dots":
      return <ChatDots {...svgProps} />;
    case "check-circle":
      return <CheckCircle {...svgProps} />;
    case "chevron-left":
      return <ChevronLeft {...svgProps} />;
    case "chevron-right":
      return <ChevronRight {...svgProps} />;
    case "clipboard":
      return <Clipboard {...svgProps} />;
    case "clipboard-check":
      return <ClipboardCheck {...svgProps} />;
    case "code":
      return <Code {...svgProps} />;
    case "cpu":
      return <Cpu {...svgProps} />;
    case "download":
      return <Download {...svgProps} />;
    case "facebook":
      return <Facebook {...svgProps} />;
    case "file-earmark":
      return <FileEarmark {...svgProps} />;
    case "file-zip":
      return <FileZip {...svgProps} />;
    case "filter":
      return <Filter {...svgProps} />;
    case "folder-fill":
      return <FolderFill {...svgProps} />;
    case "git":
      return <Git {...svgProps} />;
    case "github":
      return <Github {...svgProps} />;
    case "heart":
      return <Heart {...svgProps} />;
    case "info-circle":
      return <InfoCircle {...svgProps} />;
    case "link":
      return <Link {...svgProps} />;
    case "linkedin":
      return <LinkedIn {...svgProps} />;
    case "search":
      return <Search {...svgProps} />;
    case "star":
      return <Star {...svgProps} />;
    case "tag":
      return <Tag {...svgProps} />;
    case "tags":
      return <Tags {...svgProps} />;
    case "twitter":
      return <Twitter {...svgProps} />;
    case "x":
      return <X {...svgProps} />;

    /** custom icons */
    case "chart-up":
      // https://www.imdb.com/title/tt0381061/
      svgContent = (
        <path d="M12 2C6.5 2 2 6.5 2 12s4.5 10 10 10 10-4.5 10-10S17.5 2 12 2zm0 18c-2.6 0-5-1.3-6.4-3.3l2.8-3.4 1.3 1.5c.4.4 1 .4 1.3 0l2.9-3.2 1.3 1.4c.3.3.8.1.8-.3V8.5c0-.3-.2-.5-.5-.5h-4c-.4 0-.6.5-.3.8l1.3 1.4-2.2 2.5L9 11.3c-.4-.4-1-.4-1.3 0L4.6 15c-.4-.9-.6-1.9-.6-3 0-4.4 3.6-8 8-8s8 3.6 8 8-3.6 8-8 8z"></path>
      );
      break;
    case "chart-down":
      // https://www.imdb.com/title/tt0381061/
      svgContent = (
        <path d="M12 2C6.5 2 2 6.5 2 12s4.5 10 10 10 10-4.5 10-10S17.5 2 12 2zm0 18c-4.4 0-8-3.6-8-8 0-1.4.4-2.8 1-3.9L8.4 12c.4.4 1 .4 1.4 0l1.4-1.5 2.4 2.6-1.4 1.4c-.3.3-.1.9.4.9h4.3c.3 0 .5-.2.5-.5v-4.3c0-.4-.5-.7-.9-.3L15 11.6l-3.1-3.3c-.4-.4-1-.4-1.4 0L9.2 9.8 6.3 6.4C7.7 4.9 9.7 4 12 4c4.4 0 8 3.6 8 8s-3.6 8-8 8z"></path>
      );
      break;
    default:
      throw new Error("Unknown icon " + props.name);
  }

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={svgSize}
      height={svgSize}
      viewBox={svgViewBox}
      className="custom-icon"
    >
      {svgContent}
    </svg>
  );
}

Icon.defaultProps = {
  size: "16",
  className: "",
};

Icon.propTypes = {
  name: PropTypes.string.isRequired,
  size: PropTypes.any,
  className: PropTypes.string,
};

export default Icon;
